.triggerButton + label {
  cursor: pointer;
  position: relative;
  right: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin: 0 auto;
  background-color: #e84137;
  background-color: var(--primary);
  border-radius: 50%;
  z-index: 2;
  box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.2);
  box-shadow: var(--box-shadow); }

.triggerButton + label:before,
.triggerButton + label:after {
  position: absolute;
  content: '';
  height: calc(60px / 2);
  width: 0.25em;
  background-color: #fff;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  border-radius: 0.5em;
  -webkit-transition: all 0.25s;
  transition: all 0.25s; }

.triggerButton + label:before {
  height: calc(60px / 2);
  width: 0.25em; }

.triggerButton + label:after {
  width: calc(60px / 2);
  height: 0.25em; }

.triggerButton:checked + label:before {
  -webkit-transform: rotatez(-45deg);
          transform: rotatez(-45deg); }

.triggerButton:checked + label:after {
  -webkit-transform: rotatez(-45deg);
          transform: rotatez(-45deg); }

.one,
.two {
  cursor: pointer;
  position: absolute;
  padding: 1.5em;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  opacity: 0;
  z-index: 1;
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
  font-size: 1em;
  color: #fff;
  -webkit-transition-property: all;
  transition-property: all;
  -webkit-transition-duration: 0.35s;
          transition-duration: 0.35s;
  -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
          transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275); }

.triggerButton:checked ~ a .one,
.triggerButton:checked ~ a .two {
  opacity: 1; }

.triggerButton:checked ~ a .one {
  background-color: purple;
  -webkit-transform: translateX(-4em) translateY(-8em);
          transform: translateX(-4em) translateY(-8em);
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s; }

.triggerButton:checked ~ a .two {
